.properties-list-panel h5 {
  font-weight: normal;
  color: #999;
  font-size: 30px;
  margin: 5px 0;
  font-size: 14px;
}
.properties-list-panel .properties-list a {
  font-family: Monaco,'Source Code Pro', STHeiti, "Microsoft Yahei", "WenQuanYi Micro Hei", sans-serif;
  font-size: 12px;
  margin-right: 2px;
}
.property-popup-desc {
  max-height: 300px;
  max-width: 500px;
  overflow-y: auto;
  overflow-x: auto;
}
.property-popup-desc .hljs {
    background: transparent;
    padding: 0;
}
.property-popup-desc hr {
    border: none;
    border-bottom: 1px solid #eee;
    width: 80%;
}
.property-popup-desc blockquote {
    font-size: 12px;
    color: #999;
    background-color: #fdfdfd;
    border-left: 2px solid #ddd;
    margin-left: 0px;
    padding: 5px 10px;
}
.property-popup-desc blockquote p {
      margin: 0;
      font-size: 12px;
}
.property-popup-desc blockquote pre {
      font-size: 12px;
}
.property-popup-desc iframe {
    border: none;
    display: block;
    margin-top: 5px;
    max-width: 100%;
}
.property-popup-desc img {
    max-width: 100%;
}
.property-popup-desc p {
    line-height: 1.7em;
    margin: 12px 0 12px 0;
    font-size: 14px;
}
.property-popup-desc pre {
    margin: 5px 0;
    border-radius: 5px;
    background-color: #f5f7fa;
    border: none;
    padding: 10px;
    font-size: 13px;
}
.property-popup-desc .codespan {
    padding: 1px 5px;
    font-size: 14px;
    background-color: #f4f4f4;
    border-radius: 4px;
}
.property-popup-desc code *, .property-popup-desc code {
    font-family: 'Source Code Pro', 'Courier New', monospace;
}
.property-popup-desc ol {
    margin-left: 20px;
}
.property-popup-desc ul li {
    list-style: disc;
    margin: 10px 20px;
    font-size: 14px;
}
.property-popup-desc ol li {
    margin: 1em 0;
    list-style: decimal;
    font-size: 14px;
}
.property-popup-desc a {
    font-family: 'Source Code Pro', STHeiti, "Microsoft Yahei", "WenQuanYi Micro Hei", sans-serif;
}
.property-popup-desc pre {
    font-size: 12px;
}
.property-popup-desc p {
    font-size: 12px;
}
.property-popup-desc ul {
    margin: 0;
    padding: 0;
}
.property-popup-desc ul li {
    font-size: 12px;
    margin: 5px 20px;
    list-style: disc;
}
.property-popup-desc pre {
    padding: 5px;
}
.property-popup-desc .codespan {
    font-size: 12px;
}
