.doc-main {
  margin-left: 10px;
}
.doc-main .open-option-example {
    position: fixed;
    right: 0;
    top: 50%;
    padding: 10px;
    border-radius: 20px 0 0 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    background: #fff;
    cursor: pointer;
    font-size: 12px;
}
.doc-main .open-option-example:hover {
      background: #eee;
}
.doc-main .open-option-example i {
      font-size: 16px;
      vertical-align: middle;
}
.doc-content {
  text-align: left;
  color: #4d555e;
}
.doc-content.option-example-actived.option-example-right-layout {
    margin-right: 45%;
}
.doc-content h2 {
    color: #B03A5B;
    font-size: 34px;
    border-bottom: 1px solid #ccc;
    height: 45px;
    line-height: 45px;
    margin: 0;
    margin-left: 15px;
    font-weight: normal;
    box-sizing: content-box;
}
.doc-content h3 {
    font-weight: normal;
    color: #969696;
    font-size: 28px;
    margin: 20px 0px 20px 15px;
}
.doc-content .page-description {
    padding: 5px 0;
    margin-left: 15px;
}
.doc-content .page-description .hljs {
      background: transparent;
      padding: 0;
}
.doc-content .page-description hr {
      border: none;
      border-bottom: 1px solid #eee;
      width: 80%;
}
.doc-content .page-description blockquote {
      font-size: 12px;
      color: #999;
      background-color: #fdfdfd;
      border-left: 2px solid #ddd;
      margin-left: 0px;
      padding: 5px 10px;
}
.doc-content .page-description blockquote p {
        margin: 0;
        font-size: 12px;
}
.doc-content .page-description blockquote pre {
        font-size: 12px;
}
.doc-content .page-description iframe {
      border: none;
      display: block;
      margin-top: 5px;
      max-width: 100%;
}
.doc-content .page-description img {
      max-width: 100%;
}
.doc-content .page-description p {
      line-height: 1.7em;
      margin: 12px 0 12px 0;
      font-size: 14px;
}
.doc-content .page-description pre {
      margin: 5px 0;
      border-radius: 5px;
      background-color: #f5f7fa;
      border: none;
      padding: 10px;
      font-size: 13px;
}
.doc-content .page-description .codespan {
      padding: 1px 5px;
      font-size: 14px;
      background-color: #f4f4f4;
      border-radius: 4px;
}
.doc-content .page-description code *, .doc-content .page-description code {
      font-family: 'Source Code Pro', 'Courier New', monospace;
}
.doc-content .page-description ol {
      margin-left: 20px;
}
.doc-content .page-description ul li {
      list-style: disc;
      margin: 10px 20px;
      font-size: 14px;
}
.doc-content .page-description ol li {
      margin: 1em 0;
      list-style: decimal;
      font-size: 14px;
}
.doc-content .page-description a {
      font-family: 'Source Code Pro', STHeiti, "Microsoft Yahei", "WenQuanYi Micro Hei", sans-serif;
}
.doc-content .item-description {
    margin: 0;
    padding: 5px 0;
}
.doc-content .item-description .hljs {
      background: transparent;
      padding: 0;
}
.doc-content .item-description hr {
      border: none;
      border-bottom: 1px solid #eee;
      width: 80%;
}
.doc-content .item-description blockquote {
      font-size: 12px;
      color: #999;
      background-color: #fdfdfd;
      border-left: 2px solid #ddd;
      margin-left: 0px;
      padding: 5px 10px;
}
.doc-content .item-description blockquote p {
        margin: 0;
        font-size: 12px;
}
.doc-content .item-description blockquote pre {
        font-size: 12px;
}
.doc-content .item-description iframe {
      border: none;
      display: block;
      margin-top: 5px;
      max-width: 100%;
}
.doc-content .item-description img {
      max-width: 100%;
}
.doc-content .item-description p {
      line-height: 1.7em;
      margin: 12px 0 12px 0;
      font-size: 14px;
}
.doc-content .item-description pre {
      margin: 5px 0;
      border-radius: 5px;
      background-color: #f5f7fa;
      border: none;
      padding: 10px;
      font-size: 13px;
}
.doc-content .item-description .codespan {
      padding: 1px 5px;
      font-size: 14px;
      background-color: #f4f4f4;
      border-radius: 4px;
}
.doc-content .item-description code *, .doc-content .item-description code {
      font-family: 'Source Code Pro', 'Courier New', monospace;
}
.doc-content .item-description ol {
      margin-left: 20px;
}
.doc-content .item-description ul li {
      list-style: disc;
      margin: 10px 20px;
      font-size: 14px;
}
.doc-content .item-description ol li {
      margin: 1em 0;
      list-style: decimal;
      font-size: 14px;
}
.doc-content .item-description a {
      font-family: 'Source Code Pro', STHeiti, "Microsoft Yahei", "WenQuanYi Micro Hei", sans-serif;
}
.doc-content table {
    border: 1px solid #ddd;
    border-collapse: collapse;
}
.doc-content th, .doc-content td {
    padding: 10px;
    border: 1px solid #eee;
}
.ec-doc-tutorial .page-description h2 {
  font-weight: normal;
  font-size: 22px;
  margin-left: 0;
  margin-top: 40px;
}
.ec-doc-mobile .doc-main {
  margin-left: 0;
}
.ec-doc-mobile .doc-content {
  background: #f2f2f2;
  margin-bottom: 100px;
}
.ec-doc-mobile .page-description {
  padding: 5px 10px;
  background: #fff;
}
.ec-doc-mobile h2 {
  font-size: 22px;
  font-weight: normal;
  padding: 20px 10px;
  border-bottom: none;
}
.ec-doc-mobile h3 {
  font-size: 20px;
  padding-left: 10px;
}
