.control-percent > div {
  display: inline-block;
}
.control-percent .el-slider {
  width: 200px;
  display: inline-block;
  vertical-align: middle;
}
.control-percent .el-input-number {
  display: inline-block;
  width: 90px;
  margin-left: 10px;
}
.control-percent .el-radio-group {
  margin-right: 10px;
}
.control-percent .el-radio-button--mini .el-radio-button__inner {
  padding: 5px 4px;
  font-size: 10px;
}
